import React from 'react'
import { useParams } from 'react-router-dom'
interface TablaProps {
	nombreTabla: string
}

const Tabla: React.FC = () => {
	const props = useParams<TablaProps>()
	const headers = ['id', 'nombre', 'apellido', 'telefono', 'correo', 'ciudad', 'genero', 'direccion']
	return (
		<>
			<h3 style={styles.titulo}>Tabla {props.nombreTabla}</h3>
			<div style={styles.tabla}>
				<table className="table table-hover table-secondary">
					<thead>
						<tr className="table-primary">
							{
								headers.map(x => (
									<th scope="col" key={x}>{x}</th>
								))
							}
						</tr>
					</thead>
					<tbody>
						{
							[...Array(15)].map((_, i) => (
								<tr key={i}>
									{
										headers.map(x => (
											<td key={x}>{x}</td>
										))
									}
								</tr>
							))
						}
					</tbody>
				</table>
			</div>
			<div style={styles.arrow} role="button"></div>
		</>
	)
}

export default Tabla

interface StyleSheet {
	[nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
	tabla: {
		width: '80vw',
		height: '70vh',
		margin: 'auto',
		overflow: 'auto',
		marginBottom: '2vh'
	},
	titulo: {
		textAlign: 'center',
		marginTop: '5vh'
	},
	arrow: {
		position: 'relative',
		left: '50vw',
		height: '0',
		width: '0',
		border: '20px solid white',
		borderRightColor: 'transparent',
		borderLeftColor: 'transparent',
		borderBottomColor: 'transparent',
	},
}
